import Axios, { AxiosError } from "axios";
import { useQuery } from "react-query";
import { IQuestion } from "../interfaces/main";

const api = Axios.create({
  baseURL: "https://api.jsonbin.io/v3/b",
  headers: {
    "X-Master-Key":
      "$2b$10$VQOsammeO2AYVRv5GbMiL.G0dgSRDkJH9S0aapfcAgvJah2dju3US",
    "Content-Type": "application/json"
  }
});

export const queryConfig = {
  staleTime: 1000 * 60 * 60,
  cacheTime: 1000 * 60 * 60
};

export const useAllQuestions = () => {
  return useQuery<IQuestion[], AxiosError>({
    queryKey: ["questions"],
    queryFn: async () =>
      await api.get(`/61ae69c301558c731ccf6b89 `).then((res) => res.data),
    ...queryConfig
  });
};
