import { useState } from "react";
import "./styles.css";
import { shuffledArr } from "./utils";
import { colours } from "./data/colours";
import { useAllQuestions } from "./queries/main";

const Card = (props: any) => {
  const { title } = props;
  return (
    <div
      className={`card-Box`}
      style={{ backgroundColor: title }}
      key={title}
    ></div>
  );
};

export default function App() {
  const [colorState, setColorState] = useState<string[]>(colours);
  const [counter, setCounter] = useState<number>(0);

  const questions = useAllQuestions();

  const cardColoursMap = colorState.map((color: string) => {
    return <Card title={color} />;
  });

  const generateQuestion = () => {
    setColorState(shuffledArr(colorState));
    setCounter(counter + 1);
  };

  return (
    <div className="App">
      <h1>Start Screen</h1>
      <div>
        <button onClick={generateQuestion}>Next Question</button>
      </div>
      {cardColoursMap}
    </div>
  );
}
